import React, { useEffect, useCallback, useState } from 'react'
import { useTranslation } from 'react-i18next'
import RecipeList from './RecipeList'
import SearchBar from '@components/common/SearchBar'
import FilterControls from './FilterControls'
import AdModal from '@components/ads/AdModal'
import { useRecipeStore, selectFilteredRecipes } from '@stores/useRecipeStore'
import { useAppStore } from '@stores/useAppStore'
import { toast } from '@utils/toast'

// Î¨¥Î£å Î†àÏãúÌîº Í∞úÏàò Ï†úÌïú
const FREE_RECIPE_LIMIT = 10

const RecipeListPage: React.FC = () => {
  const { t } = useTranslation()
  const {
    recipes,
    filters,
    sortBy,
    addRecipe,
    deleteRecipe,
    setCurrentRecipe,
    setFilters,
    setSortBy,
    clearFilters,
    getAvailableTags,
    resetToSampleRecipes
  } = useRecipeStore()
  const filteredRecipes = useRecipeStore(selectFilteredRecipes)
  const availableTags = useRecipeStore(getAvailableTags)
  const { setActiveTab } = useAppStore()

  // Í¥ëÍ≥† Î™®Îã¨ ÏÉÅÌÉú
  const [showAdModal, setShowAdModal] = useState(false)

  // Ï¥àÍ∏∞ ÏßÑÏûÖ Ïãú Îπà Í≤ΩÏö∞ ÎòêÎäî Ï∂úÏ≤ò Ï†ïÎ≥¥ ÏóÜÎäî Í≤ΩÏö∞ ÏÉòÌîå Î†àÏãúÌîº Ï£ºÏûÖ
  useEffect(() => {
    // Î†àÏãúÌîºÍ∞Ä ÏóÜÏúºÎ©¥ ÏÉòÌîå Î°úÎìú
    if (!recipes || recipes.length === 0) {
      resetToSampleRecipes()
      toast.success(t('message.sampleRecipesLoaded'))
      return
    }

    // Í∏∞Ï°¥ Î†àÏãúÌîºÏóê Ï∂úÏ≤ò(source) Ï†ïÎ≥¥Í∞Ä ÏóÜÏúºÎ©¥ ÏÉòÌîåÎ°ú ÍµêÏ≤¥
    // (Ïù¥Ï†Ñ Î≤ÑÏ†Ñ Îç∞Ïù¥ÌÑ∞ ÎßàÏù¥Í∑∏Î†àÏù¥ÏÖò)
    const hasRecipesWithoutSource = recipes.some(r => !r.source)
    if (hasRecipesWithoutSource && recipes.length > 0) {
      // Î™®Îì† Î†àÏãúÌîºÏóê Ï∂úÏ≤òÍ∞Ä ÏóÜÏúºÎ©¥ ÏÉòÌîåÎ°ú ÍµêÏ≤¥
      const allWithoutSource = recipes.every(r => !r.source)
      if (allWithoutSource) {
        resetToSampleRecipes()
        toast.success(t('message.sampleRecipesUpdated'))
      }
    }
  }, [])

  const handleSelect = useCallback((recipe: any) => {
    setCurrentRecipe(recipe)
    // ÎåÄÏãúÎ≥¥ÎìúÎ°ú Ïù¥Îèô (converter ÌÉ≠ ÏÇ≠Ï†úÎê®)
    setTimeout(() => setActiveTab('dashboard'), 0)
  }, [setCurrentRecipe, setActiveTab])

  const handleDelete = useCallback((id: string) => {
    deleteRecipe(id)
  }, [deleteRecipe])

  const handleRestore = useCallback((recipe: any) => {
    addRecipe(recipe)
  }, [addRecipe])

  const handleEdit = useCallback((recipe: any) => {
    setCurrentRecipe(recipe)
    setTimeout(() => setActiveTab('editor'), 0)
  }, [setCurrentRecipe, setActiveTab])

  // Ïã§Ï†ú ÏÉà Î†àÏãúÌîº ÏÉùÏÑ± Ìï®Ïàò
  const createNewRecipe = useCallback(() => {
    const newRecipe = {
      id: `recipe-${Date.now()}`,
      name: t('recipe.newRecipe'),
      description: '',
      category: 'bread',
      method: 'straight',
      servings: 1,
      ingredients: [],
      createdAt: new Date(),
      updatedAt: new Date(),
      tags: []
    } as any
    addRecipe(newRecipe)
    setCurrentRecipe(newRecipe)
    setTimeout(() => setActiveTab('dashboard'), 0)
  }, [addRecipe, setCurrentRecipe, setActiveTab, t])

  // ÏÉà Î†àÏãúÌîº Î≤ÑÌäº Ìï∏Îì§Îü¨ (Í¥ëÍ≥† Ï≤¥ÌÅ¨)
  const handleNew = useCallback(() => {
    // Î¨¥Î£å Î†àÏãúÌîº Í∞úÏàò Ï¥àÍ≥º Ïãú Í¥ëÍ≥† Î™®Îã¨ ÌëúÏãú
    if (recipes.length >= FREE_RECIPE_LIMIT) {
      setShowAdModal(true)
    } else {
      createNewRecipe()
    }
  }, [recipes.length, createNewRecipe])

  // Í¥ëÍ≥† ÏôÑÎ£å ÌõÑ Î†àÏãúÌîº ÏÉùÏÑ±
  const handleAdComplete = useCallback(() => {
    setShowAdModal(false)
    createNewRecipe()
    toast.success(t('message.adThanks'))
  }, [createNewRecipe, t])

  const handleSearchChange = useCallback((searchQuery: string) => {
    setFilters({ ...filters, searchQuery })
  }, [filters, setFilters])

  const handleFilterChange = useCallback((newFilters: typeof filters) => {
    setFilters(newFilters)
  }, [setFilters])

  const handleSortChange = useCallback((newSortBy: typeof sortBy) => {
    setSortBy(newSortBy)
  }, [setSortBy])

  const handleClearFilters = useCallback(() => {
    clearFilters()
  }, [clearFilters])

  return (
    <div className="flex flex-col h-full">
      {/* Search and Filter Controls */}
      <div className="flex-none space-y-3 p-4 bg-bread-50 border-b border-bread-200">
        <SearchBar
          value={filters.searchQuery || ''}
          onChange={handleSearchChange}
          placeholder={t('recipeList.searchPlaceholder')}
        />
        <FilterControls
          filters={filters}
          sortBy={sortBy}
          onFilterChange={handleFilterChange}
          onSortChange={handleSortChange}
          onClearFilters={handleClearFilters}
          availableTags={availableTags}
        />
      </div>

      {/* Recipe List */}
      <div className="flex-1 overflow-auto">
        <RecipeList
          recipes={filteredRecipes}
          onSelect={handleSelect}
          onDelete={handleDelete}
          onEdit={handleEdit}
          onNew={handleNew}
          onRestore={handleRestore}
        />
      </div>

      {/* Í¥ëÍ≥† Î™®Îã¨ (10Í∞ú Ï¥àÍ≥º Ïãú) */}
      <AdModal
        isOpen={showAdModal}
        onClose={() => setShowAdModal(false)}
        onComplete={handleAdComplete}
      />
    </div>
  )
}

export default RecipeListPage

